import { apiClient } from '@api/client';
import {
  GoodsMovementSearchRequest,
  SearchGoodsMovementItem,
} from '@api/generated/apiClient.generated';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import {
  SiderWrapper,
  ListItem,
  Status,
  InvoiceNumberWrapper,
  CustomerNumberWrapper,
} from './styled';

type SiderProps = {
  currentId: number;
  onClick: (id: number) => void;
};

export const Sider: React.FC<SiderProps> = ({ currentId, onClick }) => {
  const { filter } = useParams();
  const [goodsMovements, setGoodsMovements] = useState<
    SearchGoodsMovementItem[]
  >([]);

  useEffect(() => {
    const request = JSON.parse(filter ?? '{}') as GoodsMovementSearchRequest;
    apiClient
      .goodsMovementsSearch(request)
      .then((response) => response.items)
      .then((items) => setGoodsMovements(items ?? []));
  }, []);

  return (
    <SiderWrapper>
      {goodsMovements.map((item, i) => {
        return (
          <ListItem
            key={i}
            active={item.id === currentId}
            onClick={() => onClick(item.id)}
          >
            <InvoiceNumberWrapper>
              <Status success={!item.isFailed} /> {item.id} - {item.partNumber}
            </InvoiceNumberWrapper>
            <CustomerNumberWrapper>
              {item.embossedId} ({item.licensePlate})
            </CustomerNumberWrapper>
          </ListItem>
        );
      })}
    </SiderWrapper>
  );
};
