import React, { useEffect, useState } from 'react';

import { Layout, PrimaryButton } from '@daimler/react-ux-business-app';

import { useBack } from '@hooks/useBack';

import { Sider as SiderContent } from './components/Sider';
import { Content } from './components/Content';

import { PageHeader, PageSider, PageContent } from './styled';
import { Path } from '@router/types';
import { useTranslation } from '@i10n/index';
import { GoodsMovement } from '@api/generated/apiClient.generated';
import { apiClient } from '@api/client';
import { useParams } from 'react-router-dom';

type GoodsMovementDomainProps = {};

export const GoodsMovementDomain: React.FC<GoodsMovementDomainProps> = () => {
  const handleBack = useBack(Path.GOODSMOVEMENTS);
  const { id } = useParams();
  const t = useTranslation();

  const [currentId, setCurrentId] = useState<number>(
    Number.parseInt(id ?? '-1'),
  );
  const [goodsMovement, setGoodsMovement] = useState<GoodsMovement>();

  useEffect(() => {
    apiClient.goodsMovements(currentId).then((m) => setGoodsMovement(m));
  }, [currentId]);

  const onClick = (id: number) => {
    setCurrentId(id);
  };

  return (
    <Layout>
      <PageHeader>
        <PrimaryButton onClick={handleBack}>
          {t('Rabs.Common.Back')}
        </PrimaryButton>
      </PageHeader>
      <Layout>
        <PageSider>
          <SiderContent currentId={currentId} onClick={onClick} />
        </PageSider>
        <PageContent>
          <Content goodsMovement={goodsMovement} />
        </PageContent>
      </Layout>
    </Layout>
  );
};
